import { Component, Input } from "@angular/core";
import { Car } from "../../movida-showcase/showcase.model";
import * as moment from 'moment';
import { Router } from '@angular/router';


@Component({
	selector: 'movida-card-prod-day',
	templateUrl: './card-prod-day.component.html',
	styleUrls: ['./card-prod-day.component.scss']
})
export class CardProdDayComponent {

	public countDown: any = 0

	@Input() displayTime: number = 0;
	@Input() data: Car;

	constructor(private router: Router) {}

	ngOnInit() {
		const duration = moment.duration(this.data.expiration, 's');

		const intervalId = setInterval(() => {
			duration.subtract(1, "s");
			const validHours = Math.floor(duration.asHours()) + '';
			const validMinutes = Math.floor(duration.minutes()) + '';
			const validSeconds = Math.floor(duration.seconds()) + '';
			var addZeroHours = '';
			var addZeroMinutes = '';
			var addZeroSeconds = '';
			if(validHours.length == 1){addZeroHours = '00'} else
			if(validHours.length == 2){addZeroHours = '0'} else {addZeroHours = ''}
			if(validMinutes.length == 1){addZeroMinutes = '0'} else {addZeroMinutes = ''}
			if(validSeconds.length == 1){addZeroSeconds = '0'} else {addZeroSeconds = ''}

      let totalHours = parseInt(addZeroHours+Math.floor(duration.asHours()));
			this.countDown = totalHours.toString().padStart(2, '0')+':'
			+addZeroMinutes+duration.minutes()+':'
			+addZeroSeconds+duration.seconds();

			if (duration.asSeconds() !== 0) return;
			clearInterval(intervalId);
			}, 1000);
	}
	openCar(model, brand, fipe) {
		let model_edited = (brand+'-'+model).toLowerCase().replace(/,?\s+/g, '-').replace(/[^\w-]+/g, '');
		this.router.navigate([`/assinatura/detalhes/${model_edited}/${fipe}`], { queryParamsHandling: 'merge' });
	}

}
